'use strict';

import { WixAPI } from './wixAPI';
import { decodeVIN } from './vinAPI';
import galleryCardsTemplate from '../templates/home-gallery-elements.hbs';
import multyCheckBox from '../templates/multi-checkbox-element.hbs';
import dropDown from '../templates/drop-down-element.hbs';

let searchParams = {};
let searchResults = [];
let productLine,
  productSubCategory,
  productType,
  position = [],
  subModel;

const selectSection = document.querySelector('.select-section');
const resultSection = document.querySelector('.result-section');

const formByVehicle = document.querySelector('.select-container-vehicle');
const dropDownYear = document.querySelector('#dropdown-vehicle-year');
const dropDownYearLoadingSpiner = document.querySelector('.dropdown-vehicle-year-spiner');
const dropDownMake = document.querySelector('#dropdown-vehicle-make');
const dropDownMakeLoadingSpiner = document.querySelector('.dropdown-vehicle-make-spiner');
const dropDownModel = document.querySelector('#dropdown-vehicle-model');
const dropDownModelLoadingSpiner = document.querySelector('.dropdown-vehicle-model-spiner');
const textSelectVehicleError = document.querySelector('.textSelectVehicleError');

const formByNumber = document.querySelector('.select-container-number');
const inputNumber = document.querySelector('#input-number');
const textSelectNumberError = document.querySelector('.textSelectNumberError');

const formByVin = document.querySelector('.select-container-vin');
const inputVIN = document.querySelector('#input-vin');
const textSelectVINError = document.querySelector('.textSelectVINError');

const loadingImage = document.querySelector('.parts-container-spiner');
const searchEditButton = document.querySelector('.button-result-new-search');
const clearFilterButton = document.querySelector('.button-filter-clear');
const cancelEditButton = document.querySelector('.button-cancel');
const filterContainer = document.querySelector('.filter-container');
const filterContainerFilters = document.querySelector('.filter-container-filters');
const filterProductLine = document.querySelector('#product-line');
const filterSubCategory = document.querySelector('#subCategory');
const filterType = document.querySelector('#type');
const filterSubModel = document.querySelector('#sub-model');
const filterPosition = document.querySelector('.filter-position');

const searchParamsText = document.querySelector('.result-top-container-info-text');
const resultText = document.querySelector('.result-top-container-parts-info');
const partsGallery = document.querySelector('.gallery');

const filterDropDownShema = {
  productLine: {
    text: 'PRODUCT LINE',
    title: 'product-line',
    element: filterProductLine,
    placehold: 'Select Product Line',
  },
  productSubCategory: {
    text: 'PRODUCT SUBCATEGORY',
    title: 'subCategory',
    element: filterSubCategory,
    placehold: 'Select SubCategory',
  },
  productType: {
    text: 'PRODUCT TYPE',
    title: 'type',
    element: filterType,
    placehold: 'Select Product Type',
  },
  subModels: {
    text: 'SUB MODEL',
    title: 'sub-model',
    element: filterSubModel,
    placehold: 'Select Sub Model',
  },
};

addEvents();

function addEvents() {
  formByVehicle.addEventListener('submit', onFormVehicleSubmit);
  formByNumber.addEventListener('submit', onFormNumberSubmit);
  formByVin.addEventListener('submit', onFormVINSubmit);  
  dropDownYear.addEventListener('change', onDropDownYearChange);
  dropDownMake.addEventListener('change', onDropDownMakeChange);
  dropDownModel.addEventListener('change', onDropDownModelChange);
  filterProductLine.addEventListener('change', onDropDownProductLineChange);
  filterSubCategory.addEventListener('change', onDropDownSubCategoryChange);
  filterType.addEventListener('change', onDropDownProductTypeChange);
  filterSubModel.addEventListener('change', onDropDownSubModelChange);
  filterPosition.addEventListener('change', onDropDownPositionChange);
  searchEditButton.addEventListener('click', searchEditBittonClick);
  clearFilterButton.addEventListener('click', clearFilter);
  cancelEditButton.addEventListener('click', cancelEditButtonClick);
}

const wixAPI = new WixAPI();

checkQuery();

function checkQuery() {
  let params = new URLSearchParams(document.location.search);
  readQueryParams(params);
  showInfo();
}

function readQueryParams(params) {
  const type = params.get('type');
  if (type) searchParams.type = type;
  const year = params.get('year');
  if (year) searchParams.year = year;
  const make = params.get('make');
  if (make) searchParams.make = make;
  const model = params.get('model');
  if (model) searchParams.model = model;
  const part_number = params.get('part_number');
  if (part_number) searchParams.part_number = part_number;
  const vin = params.get('vin');
  if (vin) searchParams.vin = vin;
}

function showInfo() {
  if (searchParams.type) {
    return showResults();
  }
  buildDropDownYears();
  showForm();
}

async function showResults() {
  let data = { filters: {}, parts: [] };
  try {
    loadingImage.classList.remove('hidden');
    resultText.textContent = 'Searching...';
    selectSection.classList.add('hidden');
    resultSection.classList.remove('hidden');
    if (searchParams.type == 'vehicle') {
      wixAPI.search = searchParams;
      const carDescription =
        searchParams.year + ', ' + searchParams.make + ', ' + searchParams.model;
      searchParamsText.textContent = carDescription;
      data = await wixAPI.getSearchByVehicle(searchParams);
    }

    if (searchParams.type == 'number') {
      wixAPI.search = searchParams;
      const carDescription = 'Part Number: ' + searchParams.part_number;
      searchParamsText.textContent = carDescription;
      data = await wixAPI.getSearchByNumber(searchParams);
    }

    if (searchParams.type == 'vin') {
      await getVehicleByVIN(searchParams.vin);
      wixAPI.search = searchParams;
      const carDescription = 'VIN: ' + searchParams.vin;
      searchParamsText.textContent = carDescription;
      data = await wixAPI.getSearchByNumber(searchParams);
    }

    if (data?.results) {
      searchResults = data.results.parts;
      loadingImage.classList.add('hidden');
      buildGallery(data.results.parts);
      buildFilters(data.results.filters);

      return;
    }
    showNoResults();
  } catch (error) {
    console.log(error);
    showNoResults();
  }
}

function searchEditBittonClick() {
  cancelEditButton.classList.remove('hidden');
  textSelectVehicleError.classList.add('hidden');
  textSelectNumberError.classList.add('hidden');
  textSelectVINError.classList.add('hidden');
  if (searchParams.type == 'vehicle') {
    if (!searchParams.year || !searchParams.make || !searchParams.model) {
      textSelectVehicleError.textContent = 'Select year/make/model';
      textSelectVehicleError.classList.remove('hidden');
    }
    wixAPI.search = searchParams;
    buildDropDownYears().then(() => {
      dropDownYear.value = searchParams.year;
    });
    buildDropDownMakes().then(() => {
      dropDownMake.value = searchParams.make;
    });
    buildDropDownModels().then(() => {
      dropDownModel.value = searchParams.model;
    });
  }

  if (searchParams.type == 'number') {
    buildDropDownYears();
    inputNumber.value = searchParams.part_number;
    if (searchParams.part_number?.length < 5) {
      showForm();
      textSelectNumberError.textContent = 'Please enter at least 5 characters to search';
      textSelectNumberError.classList.remove('hidden');
      return;
    }
  }

  if (searchParams.type == 'vin') {
    buildDropDownYears();
    inputVIN.value = searchParams.vin;
    if (searchParams.vin?.length != 17) {
      showForm();
      textSelectVINError.textContent = 'VIN must be 17 characters';
      textSelectVINError.classList.remove('hidden');
      return;
    }
  }
  showForm();
}

function showNoResults() {
  resultText.textContent = 'Error Search';
  selectSection.classList.add('hidden');
  resultSection.classList.remove('hidden');
  filterContainerFilters.classList.add('hidden');
  loadingImage.classList.add('hidden');
}

function buildGallery(parts) {
  partsGallery.innerHTML = '';
  resultText.textContent = `${parts?.length} PART NUMBERS`;
  partsGallery.insertAdjacentHTML('beforeend', galleryCardsTemplate(parts));
}

function buildFilters(filters) {
  console.log(filters);
  const keys = Object.keys(filters);
  if (!keys.length) {
    filterContainerFilters.classList.add('hidden');
  }
  filterContainerFilters.classList.remove('hidden');
  for (let index = 0; index < keys.length; index++) {
    if (keys[index] == 'positions') {
      filterPosition.innerHTML = ` <legend>POSITION</legend>
      ${multyCheckBox({ options: filters.positions })} `;
      filterPosition.classList.remove('hidden');
    } else {
      if (filterDropDownShema[keys[index]]) {
        filterDropDownShema[keys[index]].element.innerHTML = dropDown({
          options: filters[keys[index]],
          title: filterDropDownShema[keys[index]].title,
          placehold: 'Select Filter',
        });

        filterDropDownShema[keys[index]].element.parentNode.classList.remove('hidden');
      }
    }
  }
}

function showForm() {
  selectSection.classList.remove('hidden');
  resultSection.classList.add('hidden');
}

async function buildDropDownYears() {
  try {
    dropDownYearLoadingSpiner.classList.remove('hidden');
    dropDownYear.disabled = true;
    const yearsOptions = await wixAPI.getYears();
    dropDownYear.innerHTML = '';
    dropDownYear.insertAdjacentHTML(
      'beforeend',
      dropDown({ options: yearsOptions, title: 'dropdown-vehicle-year', placehold: 'Select Year' }),
    );
    dropDownYearLoadingSpiner.classList.add('hidden');
    dropDownYear.disabled = false;
  } catch (error) {
    dropDownYearLoadingSpiner.classList.add('hidden');
    console.log(error);
  }
}

function onDropDownYearChange(event) {
  wixAPI.updateSearch({ year: event.target.value });
  buildDropDownMakes();
  dropDownModel.disabled = true;
  dropDownModel.selectedIndex = 0;
}

function onDropDownMakeChange(event) {
  wixAPI.updateSearch({ make: event.target.value });
  buildDropDownModels();
}

function onDropDownModelChange(event) {
  console.log(event);
  wixAPI.updateSearch({ model: event.target.value });
}

function buildDropDownMakes() {
  dropDownMakeLoadingSpiner.classList.remove('hidden');
  dropDownMake.disabled = true;
  return wixAPI
    .getMakes()
    .then(makesOptions => {
      dropDownMake.innerHTML = dropDown({
        options: makesOptions,
        title: 'dropdown-vehicle-make',
        placehold: 'Select Make',
      });
      dropDownMake.disabled = false;
    })
    .catch(error => {
      console.log(error);
    })
    .finally(() => {
      dropDownMakeLoadingSpiner.classList.add('hidden');
    });
}

function buildDropDownModels() {
  dropDownModelLoadingSpiner.classList.remove('hidden');
  dropDownModel.disabled = true;
  return wixAPI
    .getModels()
    .then(modelsOptions => {
      (dropDownModel.innerHTML = dropDown({
        options: modelsOptions,
        title: 'dropdown-vehicle-model',
        placehold: 'Select Model',
      })),
        (dropDownModel.disabled = false);
    })
    .catch(error => {
      console.log(error);
    })
    .finally(() => {
      dropDownModelLoadingSpiner.classList.add('hidden');
    });
}

function onDropDownProductLineChange(event) {
  productLine = event.target.value;
  filterResults();
}

function onDropDownSubCategoryChange(event) {
  productSubCategory = event.target.value;
  filterResults();
}

function onDropDownProductTypeChange(event) {
  productType = event.target.value;
  filterResults();
}

function onDropDownSubModelChange(event) {
  subModel = event.target.value;
  filterResults();
}

function onDropDownPositionChange(event) {
  const positionCheckBoxes = document.getElementsByClassName('checkbox-position');
  position = [];
  for (let index = 0; index < positionCheckBoxes.length; index++) {
    const element = positionCheckBoxes[index];
    if(element.checked == true) {
      position.push(element.value)
    }
  }  
  filterResults();
}

function onFormVehicleSubmit(event) {
  event.preventDefault();
  const formData = new FormData(formByVehicle);
  searchParams = {
    type: 'vehicle',
    year: formData.get('dropdown-vehicle-year'),
    make: formData.get('dropdown-vehicle-make'),
    model: formData.get('dropdown-vehicle-model'),
  };
  addQueryParams(searchParams);
  showResults();
  inputNumber.textContent = "";
  inputVIN.textContent = "";
}

function onFormNumberSubmit(event) {
  event.preventDefault();
  textSelectNumberError.classList.add('hidden');
  const formData = new FormData(formByNumber);
  const part_number = formData.get('input-nember');

  if (part_number?.length < 4) {
    textSelectNumberError.textContent = 'Please enter at least 4 characters to search';
    textSelectNumberError.classList.remove('hidden');
    return;
  }

  searchParams = {
    type: 'number',
    part_number: formData.get('input-number'),
  };
  addQueryParams(searchParams);
  showResults();
  inputVIN.textContent = "";
  dropDownYear.selectedIndex = 0;
  dropDownMake.selectedIndex = 0;
  dropDownMake.disabled = true;
  dropDownModel.selectedIndex = 0;
  dropDownModel.disabled = true;
}

function onFormVINSubmit(event) {
  event.preventDefault();
  textSelectVINError.classList.add('hidden');
  const formData = new FormData(formByVin);
  const vinNumber = formData.get('input-vin');

  if (vinNumber.length !== 17) {
    textSelectVINError.textContent = 'VIN must be 17 characters';
    textSelectVINError.classList.remove('hidden');
    return;
  } else {
    searchParams = {
      type: 'vin',
      vin: vinNumber,
    };
    addQueryParams(searchParams);
    showResults();
    inputNumber.textContent = "";
  }
}

function addQueryParams(addParams) {
  const url = new URL(window.location.href);

  const urlParams = new URLSearchParams([]);

  const keys = Object.keys(addParams);
  for (let index = 0; index < keys.length; index++) {
    urlParams.set(keys[index], addParams[keys[index]]);
  }
  const newParams = urlParams.toString();
  console.log(newParams);
  const newUrl = new URL(`${url.origin}${url.pathname}?${newParams}`);
  window.history.replaceState(null, null, newUrl);
}

function getVehicleByVIN(vin) {
  return decodeVIN(vin)
    .then(res => {
      const { ModelYear, Make, Model } = res.Results[0];
      if (ModelYear?.length && Make?.length && Model?.length) {
        return (searchParams = {
          vin: vin,
          type: 'vin',
          year: ModelYear,
          make: Make,
          model: Model,
        });
      }
      throw 'Error VIN Decode';
    })
    .catch(error => {
      console.log(error);
    });
}

function filterResults() {
  let searchResultsFiltered = [...searchResults];
  if (position.length) {
    searchResultsFiltered = searchResultsFiltered.filter(el => {
      if ('position' in el) {
        for (let index = 0; index < position.length; index++) {
          const elementPosition = position[index];
          if (el.position.includes(elementPosition)) {
            return true;
          }
        }
        return false;
      } else return true;
    });
  }

  if (subModel) {
    searchResultsFiltered = searchResultsFiltered.filter(el => {
      if ('subModel' in el) {
        return el.subModel.includes(subModel);
      } else return true;
    });
  }

  if (productLine) {
    searchResultsFiltered = searchResultsFiltered.filter(el => {
      if ('category' in el) {
        return el.category.includes(productLine);
      } else return true;
    });
  }

  if (productType) {
    searchResultsFiltered = searchResultsFiltered.filter(el => {
      if ('partType' in el) {
        return el.partType.includes(productType);
      }
      return true;
    });
  }

  if (productSubCategory) {
    searchResultsFiltered = searchResultsFiltered.filter(el => {
      if ('subCategory' in el) {
        return el.subCategory.includes(productSubCategory);
      } else return true;
    });
  }

  resultText.textContent = searchResultsFiltered?.length + ' PART NUMBERS';
  buildGallery(searchResultsFiltered);
}

function clearFilter() {
  filterProductLine.selectedIndex = 0;
  productLine = undefined;
  filterSubCategory.selectedIndex = 0;
  productSubCategory = undefined;
  filterType.selectedIndex = undefined;
  productType = undefined;
  filterSubModel.selectedIndex = undefined;
  subModel = undefined;
  clearFilterPasition();
  position = [];
  resultText.textContent = searchResults?.length + ' PART NUMBERS';
  filterResults();
}

function clearFilterPasition() {
  const positionCheckBoxes = document.getElementsByClassName('checkbox-position');
  for (let index = 0; index < positionCheckBoxes.length; index++) {
    const element = positionCheckBoxes[index];
    element.checked = false;
  }
}

function cancelEditButtonClick() {
  selectSection.classList.add('hidden');
  resultSection.classList.remove('hidden');
}
