'use strict';

import { deCodeVIN } from './vinAPI';

export class WixAPI {
  #BASE_URL = 'https://www.gspnorthamerica.com/_functions';

  constructor() {    
    this.search = {};
    this.filters = {};    
  }

  getYears() {
    return fetch(`${this.#BASE_URL}/years`).then(response => {
      if (!response.ok) {
        throw new Error(response.status);
      }
      return response
        .json()
        .then(result => result.years)
        .catch(err => {
          console.log(err);
        });
    });
  }

  getMakes() {
    return fetch(`${this.#BASE_URL}/makes?year=${this.search.year}`).then(response => {
      if (!response.ok) {
        throw new Error(response.status);
      }
      return response
        .json()
        .then(result => result.makes)
        .catch(err => {
          console.log(err);
        });
    });
  }

  getMakesList() {
    return fetch(`${this.#BASE_URL}/makes`).then(response => {
      if (!response.ok) {
        throw new Error(response.status);
      }
      return response
        .json()
        .then(result => result)
        .catch(err => {
          console.log(err);
        });
    });
  }

  getModels() {
    return fetch(`${this.#BASE_URL}/models?year=${this.search.year}&make=${this.search.make}`).then(
      response => {
        if (!response.ok) {
          throw new Error(response.status);
        }
        return response
          .json()
          .then(result => result.models)
          .catch(err => {
            console.log(err);
          });
      },
    );
  }

  getModelsList() {
    return fetch(`${this.#BASE_URL}/models`).then(response => {
      if (!response.ok) {
        throw new Error(response.status);
      }
      return response
        .json()
        .then(result => result.models)
        .catch(err => {
          console.log(err);
        });
    });
  }

  updateSearch(newParams) {
    this.search = {
      ...this.search,
      ...newParams
    }
  }

  getSearchByVehicle() {
    return fetch(
      `${this.#BASE_URL}/search?year=${this.search.year}&make=${this.search.make}&model=${this.search.model}&type=${this.search.type}`,
    ).then(response => {
      if (!response.ok) {
        throw new Error(response.status);
      }
      return response
        .json()
        .then(result => result)
        .catch(err => {
          console.log(err);
        });
    });
  }

  getSearchByNumber() {
    return fetch(`${this.#BASE_URL}/search?part_number=${this.search.part_number}&type=${this.search.type}`).then(
      response => {
        if (!response.ok) {
          throw new Error(response.status);
        }
        return response
          .json()
          .then(result => result)
          .catch(err => {
            console.log(err);
          });
      },
    );
  }

  getVehicleByVIN(vin) {
    return deCodeVIN(vin).then(results => {
      if (results.Results.length) {
        vinCode = results.Results[0];
        let { Make, Model, ModelYear } = vinCode;
        this.search = {
          year: ModelYear,
          make: Make,
          model: Model,
        };
      }
    });
  }
}
